.main {
    position: relative;
    height: 100%;
    width: 100%;
    max-width: 412px;
    padding: 0;
}

/*==================BACKGROUND===================*/
.home-bg {
    z-index: -1;
    height: 100%;
    width: 99%;
    font-size: 0;
    position: absolute;
}

.videoTag {
    object-fit: cover;
    width: 100%;
    height: 33.33%;
}

.home-bg-gradient {
    z-index: 1;
    height: 100%;
    width: 100%;
    position: absolute;
    background-image: linear-gradient(transparent, rgb(90, 36, 7));
    opacity: 0.5;
}


/*==================FOREGROUND===================*/

