.home-content {
    height: 92%;
    z-index: 3;
    text-align: center;
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
    gap: 24px;
}

.home-content-header-opacity-none, .home-content-details-opacity-none{
    opacity: 0;
    transition: all 1s ease-in-out;
}

.home-content-header-opacity-full, .home-content-details-opacity-full{
    opacity: 1;
    transition: all 1s ease-in-out;
}


.home-content-details{
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
    gap: 4px;
}

.date-time-section {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
}

.save-date{
    margin-left: -5px;
}

.date-time-middle h4{
    margin-left: -6px;
}

.date-time-first, .date-time-last{
    width: 71px;
    position: relative;
}

.date-time-first::after, .date-time-last::after {
    content: "";
    position: absolute;
    left: 0;
    height: 16px;
    width: 100%;
    border-top: 1px solid white;
}

.date-time-first::before, .date-time-last::before {
    content: "";
    position: absolute;
    height: 16px;
    left: 0;
    width: 100%;
    border-top: 1px solid white;
}

@media (min-width: 375px) {

    .home-content{
        gap: 22px;
    }

    .date-time-first, .date-time-last{
        width: 86px;
    }
}

@media (min-width: 412px) {

    .home-content{
        gap: 28px;
    }

    .date-time-first, .date-time-last{
        width: 90px;
    }
}